.test-table {
  width: 100%;
}

.table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #000;
}

.table .head {
  background-color: #eee;
}

.table th,
.table td {
  border: 1px solid #000;
  vertical-align: top;
}

.head .head-cell-inner {
  display: flex;
  flex-direction: column;
  padding: 5px;
  font-size: 12px;
  text-align: center;
}

.head .head-cell-inner.small {
  font-size: 10px;
}

.head .head-cell-inner.mobile {
  display: none;
}

.body .body-cell-inner {
  padding: 5px;
  font-size: 12px;
}

.body .body-cell-inner.flex {
  display: flex;
  flex-direction: column;
}
.body .body-cell-inner.center {
  text-align: center;
}

.body-cell__first {
  width: 200px;
}

.link {
  color: #00507d;
}

.link img {
  float: left;
  width: 65px;
  margin-right: 5px;
}

.show-mobile {
  display: none;
}

@media screen and (max-width: 610px) {
  .hidden {
    display: none;
  }
  .head .head-cell-inner.desktop {
    display: none;
  }
  .head .head-cell-inner.mobile {
    display: block;
    text-align: center;
    word-break: break-word;
  }
  .head-cell-inner.last-cell {
    display: block;
    text-align: center;
    word-break: break-word;
  }
  .body-cell__first .body-cell-inner {
    text-align: center;
  }
  .link {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .link img {
    float: none;
    margin-top: 5px;
    margin-right: 0;
  }
  .show-mobile {
    display: block;
  }
}
