.bottom-section {
  margin-top: 50px;
  font-size: 12px;
}

.qr-section {
  display: flex;
}

.qr-section .qr-img {
  width: 128px;
  height: 128px;
  margin-top: 5px;
  margin-right: 10px;
}

@media screen and (max-width: 500px) {
  .qr-section {
    flex-direction: column;
    align-items: center;
  }
  .bottom-section {
    text-align: center;
  }
  .hidden {
    display: none;
  }
}
