.top-section {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.med-info {
  width: 100%;
  font-weight: bold;
  text-align: left;
  line-height: 1.5;
  padding: 5px 15px 5px 0;
}

.armed-icon {
  max-width: 200px;
  max-height: 90px;
  object-fit: contain;
}

@media screen and (max-width: 610px) {
  .top-section {
    flex-wrap: wrap;
    justify-content: center;
  }
  .med-info {
    text-align: center;
  }
}
