.bio {
  display: flex;
}

.bio img {
  width: 150px;
  max-height: 190px;
  object-fit: contain;
}

.bio-info {
  width: 100%;
}

.bio-info-inner {
  padding-left: 20px;
}

.patient-info {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  text-align: left;
}

.patient-info-inner {
  display: flex;
  flex-direction: column;
  margin: 5px 0;
  padding-left: 10px;
  font-size: 13px;
  font-style: italic;
  line-height: 1.5;
}

.patient-info-inner strong {
  font-size: 14.4px;
}

.patient-info-inner.info-val {
  text-transform: uppercase;
  font-style: normal;
  line-height: normal;
}

.patient-info-inner.first {
  min-width: 300px;
}

@media screen and (max-width: 500px) {
  .bio {
    flex-direction: column;
    align-items: center;
  }

  .bio-info-inner {
    padding: 0;
  }

  .patient-info {
    flex-direction: column;
  }

  .patient-info-inner {
    text-align: center;
  }
}
