.cert-section {
  margin-bottom: 25px;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 19.2px;
  font-weight: bold;
  text-align: center;
  background-color: #42816c;
  color: white;
}

.cert-section span {
  margin-bottom: 5px;
}

@media screen and (max-width: 610px) {
  .cert-section {
    font-size: 16px;
  }
}
